
import { defineComponent, onMounted, ref } from "vue";
import { toPDFfromHTML } from "@/utils/Export";
import {
    IonTitle,
    IonPage,
    IonToolbar,
    IonContent,
    IonHeader,
    IonFooter,
    IonButton,
    IonImg,
    IonThumbnail,
    loadingController,
    modalController
} from "@ionic/vue"
import LegacyTemplate from "./LegacyTemplate.vue"
import { DrillPatients } from "@/utils/PatientDrilldown";
import { MultiStepPopupForm } from "@/utils/PopupKeyboard"
import { FieldType } from '@/components/Forms/BaseFormElements'
import { Option } from '@/components/Forms/FieldInterface'
import { isEmpty, isPlainObject } from "lodash";
import Validation from "@/components/Forms/validations/StandardValidations"
import HisDate from "@/utils/Date"
import { HtsReportService } from "@/apps/HTS/services/hts_report_service"
import { toastDanger } from "@/utils/Alerts";
import Img from "@/utils/Img"

const indicators = ref<any>()
const report = ref<Option>()
const year = ref('')
const month = ref('')
const facility = HtsReportService.getLocationName()||'Unknown'

export default defineComponent({
    components: {
        IonPage,
        IonImg,
        IonThumbnail,
        IonTitle,
        IonToolbar,
        IonContent,
        IonHeader,
        IonFooter,
        IonButton,
        LegacyTemplate
    },
    setup() {
        const logo = Img('login-logos/Malawi-Coat_of_arms_of_arms.png')

        function drilldown(label: string, clients: number[]) {
            DrillPatients(label, clients||[])
        }
        
        async function generateReport() {
            if (!year.value && !month.value) {
                return
            }
            const service = new HtsReportService(`${report.value?.value}`)
            const date = `${year.value}-${month.value}-01`
            service.startDate = date
            service.endDate = date;
            (await loadingController.create({
                message: "Please wait"
            })).present()
            try {
                indicators.value = await service.requestReport()
            } catch (e) {
                toastDanger("Unable to generate report")
            }
            loadingController.getTop().then(v => v && loadingController.dismiss())
        }

        function configureReport() {
            MultiStepPopupForm([
                {
                    id: 'report',
                    helpText: "Report name",
                    type: FieldType.TT_SELECT,
                    isRequired: () => true,
                    computedValue: (v: Option) => v.value,
                    options: () => {
                        return [
                            { label: "HIV Testing (Regular Clients) – Combined Quarterly Site Report", value: "HTS LEGACY REGULAR" },
                            { label: "HIV Testing (Regular returning Clients) – Combined Quarterly Site Report", value: "HTS LEGACY RETURNING" }
                        ]
                    }
                },
                {
                    id: 'year',
                    helpText: 'Select Year',
                    type: FieldType.TT_NUMBER,
                    isRequired: () => true,
                    computedValue: (v: Option) => v.value,
                    validation: (v: Option) => {
                        const year = isPlainObject(v) ? v.value : -1
                        return Validation.validateSeries([
                            () => {
                                if (isNaN(parseInt(`${year}`))) {
                                    return ['Invalid year']
                                }
                                return null
                            },
                            () => Validation.rangeOf(v, 2000, HisDate.getYear(HtsReportService.getSessionDate()))
                        ])
                    }
                },
                {
                    id: 'month',
                    helpText: 'Select Month',
                    type: FieldType.TT_SELECT,
                    isRequired: () => true,
                    computedValue: (v: Option) => v.value,
                    options: () => {
                        return [
                            {label: 'January', value: '01'},
                            {label: 'February', value: '02'},
                            {label: 'March', value: '03'},
                            {label: 'April', value: '04'},
                            {label: 'May', value: '05'},
                            {label: 'June', value: '06'},
                            {label: 'July', value: '07'},
                            {label: 'August', value: '08'},
                            {label: 'September', value: '09'},
                            {label: 'October', value: '10'},
                            {label: 'November', value: '11'},
                            {label: 'December', value: '12'}
                        ]
                    }
                }
            ],
            {
                skipSummary: true,
                formLabel: "Legacy report",
                onFinish: (f: any, c: any) => {
                    year.value = c.year
                    month.value = c.month
                    report.value = f.report
                    generateReport()
                    modalController.dismiss()
                }
            })
        }

        function downloadPDF() {
            const content = document.getElementById("report-content")
            if (content) {
                toPDFfromHTML(`
                    <html>
                        <body>
                            ${content.innerHTML}
                        </body>
                    </html>
                `, 50)
            }
        }

        onMounted(() => {
            if (isEmpty(indicators.value)) {
                configureReport()
            }
        })
        return {
            facility,
            year,
            indicators,
            report,
            logo,
            drilldown,
            downloadPDF,
            configureReport,
            generateReport
        }
    }
})
